<template>
  <div id="app">
    <h1>Money Creation Simulator</h1>
    <input v-on:click="askLoan" type="button" value="Ask for a Loan" />
    <input v-on:click="buyIco" type="button" value="Buy Tokens" />
    <input v-on:click="drawCash" type="button" value="Draw Cash" />
    <input v-on:click="mineBitcoin" type="button" value="Mine Bitcoin" />
    <input v-on:click="buyBitcoin" type="button" value="Buy Bitcoin" />
    <h3>Money in the System is {{ totalMoney }} USD</h3>
    <div id="NewStyle">
      <Ledger title="bank" />
      <Ledger title="me" />
      <Ledger title="startup" />
      <Ledger title="other" />
    </div>
    <button @click="drawChart">Click me</button>
    <div id="dashboard"></div>
  </div>
</template>

<script>
// import store from "./store";
import Ledger from "./ledger";
import { dashboard } from "./dashboard.js";
import { rules } from "./rules.js";
export default {
  name: "app",
  components: {
    Ledger
  },
  data() {
    return {
      ledgers: ["bank", "me", "startup", "other"],
      moneyAccounts: ["Current Account", "Bitcoins", "Tokens", "Cash"],
      freqData: [
        { Ledger: "AL", bs: { Bitcoins: 4786, Cash: 1319, Tokens: -249 } },
        {
          Ledger: "AZ",
          bs: { low: 1101, Bitcoins: 412, "Current Account": 674 }
        },
        {
          Ledger: "CT",
          bs: { "Current Account": 932, Tokens: 2149, Cash: 418 }
        },
        {
          Ledger: "DE",
          bs: { Cash: 832, Bitcoins: 1152, "Current Account": 1862 }
        },
        { Ledger: "FL", bs: { Bitcoins: 4481, mid: 3304, Tokens: 948 } },
        {
          Ledger: "GA",
          bs: { Cash: 1619, "Current Account": 167, Bitcoins: 1063 }
        },
        { Ledger: "IA", bs: { low: 1819, Tokens: 247, Cash: 1203 } },
        { Ledger: "IL", bs: { "Current Account": 4498, mid: 3852, high: 942 } },
        { Ledger: "IN", bs: { low: 797, "Current Account": 1849, high: 1534 } },
        { Ledger: "KS", bs: { low: 162, mid: 379, "Current Account": 471 } }
      ]
    };
  },
  methods: {
    askLoan: function() {
      rules.askLoan(this.$store);
    },
    buyIco: function() {
      rules.buyIco(this.$store);
    },
    drawCash: function() {
      rules.drawCash(this.$store);
    },
    mineBitcoin: function() {
      rules.mineBitcoin(this.$store);
    },
    buyBitcoin: function() {
      rules.buyBitcoin(this.$store);
    },
    drawChart: function() {
      var chartData = [];

      var ledgers = this.$data.ledgers;
      for (var i = 0; i < ledgers.length; i++) {
        var accounts = {};
        for (var account in this.$store.state[ledgers[i]]) {
          var balance = this.$store.state[ledgers[i]][account];
          accounts[account] = balance;
        }
        chartData.push({ Ledger: ledgers[i], bs: accounts });
      }
      dashboard.drawchart("#dashboard", chartData);
      // dashboard.drawchart("#dashboard", { this.$store });
    }
  },
  computed: {
    totalMoney() {
      var money = 0;
      var ledgers = this.$data.ledgers;
      var moneyAccounts = this.$data.moneyAccounts;
      for (var i = 0; i < ledgers.length; i++) {
        for (var account in this.$store.state[ledgers[i]]) {
          var balance = this.$store.state[ledgers[i]][account];
          if (moneyAccounts.indexOf(account) > -1) money += balance;
        }
      }
      return money;
    }
  }
};
</script>

